.toparea {
    display: table;
    table-layout: fixed;
    border-collapse: collapse;
    width: 100%;
    height: 100vh;
    color: #FFF;
    background: img-url('toparea.jpg') no-repeat center center;
    background-size: cover;

    @include r(950) {
        .toparea__inner {
            padding-bottom: 105px;
        }

        .toparea__footer {
            display: block;
        }
    }
}

.toparea__inner {
    display: table-cell;
    position: relative;
    width: 100%;
    height: 100%;
    vertical-align: middle;
    padding: 115px 0 40px;

    .header {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
    }

    .subtitle {
        max-width: 960px;
        margin: auto;
        margin-top: 10px;
        text-align: center;
    }
}

.toparea__footer {
    display: none;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 65px;
    text-align: center;
}


.header {
    padding: 35px 0;

    .btn {
        display: inline-block;
        vertical-align: middle;
        margin-left: 15px;
    }

    @include r(950) {
        .btn {
            display: none;
        }
    }

    @include r(700) {
        padding: 25px 0;
        .header__left {
            float: none;
            margin: auto;
            text-align: center;
        }

        .header__right {
            float: none;
            margin: 15px auto 0;
            text-align: center;
        }

    }

    @include mobile {
        .menu__link {
            font-size: 14px;
        }
    }
}

.header__inner {
    @include clr;
}

.header__left {
    float: left;
}

.header__right {
    float: right;
}

.header_mod {
    border-bottom: 1px solid $border;
}



.logo {
    display: block;
    margin: auto;
    text-decoration: none;
    @include hide-text;
    // @include sprite(logo)
    background-image: url('../img/logo-white.svg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    @include fs(42);
    width: em(284,42);
    height: em(42,42);
}

.menu {
    font-size: 0;
    display: inline-block;
    vertical-align: middle;

    @include r(1000) {
        .menu__link {
            margin-right: 0;
        }
    }
}

.menu__link {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    @include serif;
    @include fs(18);
    padding: 0 10px;
    margin-right: 10px;
    line-height: em(40,18);
    color: inherit;
    text-decoration: none;

    &:last-child {
        margin-right: 0;
    }

    &.is-active {
        font-weight: bold;
        @include serif-b;

        &:before {
            content: "";
            position: absolute;
            bottom: 2px;
            left: 50%;
            width: 4px;
            height: 4px;
            margin-left: -2px;
            background: #FFF;
            border-radius: 50%;
        }
    }
}


.btn {
    display: inline-block;
    position: relative;
    background: none;
    box-sizing: border-box;
    height: 40px;
    min-width: 175px;
    padding: 0 15px;
    border: 1px solid;
    border-radius: 3px;
    color: inherit;
    text-align: center;
    @include sans;
    font-size: 18px;

    > span {
        display: inline-block;
        padding-right: em(15);
        position: relative;

        &:before {
            @extend %icon;
            content: "\f178";
            position: absolute;
            top: 50%;
            right: 0;
            // @include sprite(arr);
            @include fs(10);
            margin-top: em(-3,10);
        }
    }

    @include mobile {
        font-size: 12px;
        min-width: 140px;

        span:before {
            font-size: 8px;
        }
    }
}

a.btn {
    line-height: 40px;
    text-decoration: none;
    &:hover {
        text-decoration: none;
    }
}

.btn_sign {
    width: 175px;
}

.btn_blue {
    background: $blue;
    color: #FFF;
    border: none;
}

.btn_orange {
    background: $orange;
    color: $text;
    border: none;
}

.btn_purple {
    background: $purple;
    color: #FFF;
    border: none;
}

.btn_dark {
    background: $text;
    color: #FFF;
    border: none;
}

.btn_center {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.btn_wide {
    min-width: 270px;
    @include mobile {
        min-width: 140px;
    }
}

.btn_more {
    background: none;
    border: 3px solid #D8D8D8;
    border-radius: none;

    > span {
        padding: 0 0 0 em(15);
        &:before {
            content: "";
            right: auto;
            left: 0;
            width: 0;
            height: 0;
            font-size: inherit;
            border: solid transparent;
            border-width: em(6) 0 em(6) em(6);
            border-left-color: $text;
            margin-top: em(-6);
        }
    }
}


.toparea-small {
    position: relative;
    padding: 40px percentage(130/1224) 45px;
    background: $toparea;
    text-align: center;
    &:after {
        content: "";
        position: absolute;
        top: 100%;
        left: 50%;
        @include arr(30,18,$toparea,b);
    }
}


.heading {
    display: block;
    padding: 24px;
    border: 3px solid #FFF;
    @include fs(72);
    @include sans;
    color: #FFF;
    max-width: 960px;
    margin: auto;
    text-align: center;

    del {
        text-decoration: none;
        display: inline-block;
        position: relative;
        margin: 0;
        &:before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            border: em(2,72) solid;
            margin-top: em(5,72);
        }
    }

    @include r(600) {
        @include fs(42);
    }

    @include mobile {
        @include fs(36);
    }
}

.heading_toparea {
    @include tablet {
        background: rgba(#FFF, 0.2);
        border: none;
    }
}


.title {
    display: block;
    @include sans;
    @include fs(36);
    line-height: 1.33;
    color: inherit;

    @include mobile {
        font-size: 24px;
        line-height: 1;
    }
}

.title_blue {
    color: $blue;
}

.title_orange {
    color: $orange;
}

.title_purple {
    color: $purple;
}


.subtitle {
    display: block;
    @include serif;
    @include fs(24);
    color: inherit;

    @include mobile {
        font-size: 14px;
    }
}



.user {
    display: block;
    position: relative;
    box-sizing: border-box;
    @include fs(18);
    color: #949494;
    min-height: em(80,18);
    padding-left: em(95,18);
    white-space: nowrap;

    &:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        width: 0;
        height: 100%;
        min-height: em(80,18);
    }
}

.user__pic {
    display: block;
    position: absolute;
    box-sizing: border-box;
    // top: 50%;
    top: 0;
    left: 0;
    width: em(80,18);
    height: em(80,18);
    border: em(3,18) solid #949494;
    border-radius: 50%;
    // margin-top: em(-40,18);
    overflow: hidden;

    img {
        display: block;
        max-width: 100%;
        max-height: 100%;
    }
}

.user__feedback {
    display: inline-block;
    vertical-align: middle;
    white-space: normal;
    @include serif;
    margin: 0;
    padding: 0;
    font-size: 1em;
    margin-left: em(-5,18);
    color: inherit;
}

.user__name {
    display: inline-block;
    margin-right: em(15,18);
    margin-top: em(7,18);
    @include sans-i;
}

.user__link {
    display: inline-block;
    position: relative;
    padding-right: em(15,18);
    margin-top: em(7,18);
    color: inherit;
    text-decoration: none;
    border-bottom: 1px solid;
    @include sans;

    &:after {
        @extend %icon;
        content: "\f178";
        position: absolute;
        top: 50%;
        right: 0;
        // @include sprite(arr);
        @include fs(10);
        margin-top: em(-3,10);
    }

}

.user_replace {
    padding-left: 0;
    padding-right: em(95);
    text-align: right;

    .user__pic {
        left: auto;
        right: 0;
    }

    .user__name {
        margin-right: 0;
    }

    .user__link {
        margin-left: em(15,18);
    }
}



.feature {
    padding: 60px 0;
    @include fs(18);
    line-height: 1.2;
    @include serif;
    overflow: hidden;

    .title {
        margin-bottom: em(10,36);
    }

    .btn {
        display: block;
        margin-top: 22px;
    }

    .user {
        margin-top: 50px;
        max-width: 520px;
    }

    @include tablet {

        .feature__outer {
            padding-left: 0;
            padding-right: 0;
            &:before {
                display: none;
            }
        }

        .feature__inner {
            display: block;
        }

        .feature__pic {
            position: relative;
            width: 100%;
            padding: 25px 0;
            top: 0;
            right: 0;
            transform: none;
            background-position: center center;
            margin: auto;
            &:before {
                position: relative;
                z-index: 1;
            }
            &.capture {
                max-width: (500/2)+px;
                &:before {
                    padding-bottom: percentage(340/500);
                    background-image: img-url('capture-small.png');
                }
            }
            &.find {
                max-width: (582/2)+px;
                &:before {
                    padding-bottom: percentage(488/582);
                    background-image: img-url('find-small.png');
                }
            }
        }

        .feature__slider {
            position: static;
            width: auto;
            transform: none;
            margin: 25px -15px;
        }

        .btn {
            margin-left: auto;
            margin-right: auto;
        }

        .user {
            max-width: none;
            color: $text;
        }
    }

    @include mobile {
        padding: 30px 0;

        .feature__text {
            font-size: 14px;
        }

        .user {
            margin-top: 25px;
        }

        .user__name, .user__link {
            font-size: 14px;
        }
    }
}

.feature__outer {
    position: relative;
    padding-right: percentage(480/1124);
    font-size: 0;

    &:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        width: 0;
        height: 100%;
        min-height: 470px;
    }
}

.feature__inner {
    display: inline-block;
    vertical-align: middle;
    @include fs(18);
    @include clr;
}

.feature__pic {
    position: absolute;
    top: 50%;
    right: -50px;
    width: percentage(480/1124);
    transform: translateY(-50%);

    &:before {
        content: "";
        display: block;
        width: 100%;
        padding-bottom: percentage(470/480);
        background-repeat: no-repeat;
        background-position: right center;
        background-size: contain;
    }

    &.capture:before {
        background-image: img-url('capture.png');
    }

    &.find:before {
        background-image: img-url('find.png');
    }
}

.feature__slider {
    position: absolute;
    top: 50%;
    right: -50px;
    width: percentage(415/1124);
    // height: 400px;
    // margin-top: -200px;
    transform: translateY(-50%);
    overflow: hidden;
}

.feture_replace {
    background: $text;
    color: #FFF;

    .feature__outer {
        padding-left: percentage(480/1124);
        padding-right: 0;
    }

    .feature__pic {
        right: auto;
        left: -50px;
        &:before {
            background-position: left center;
        }
    }

    .user {
        float: right;
    }

    @include tablet {

        .feature__outer {
            padding-left: 0;
        }

        .feature__pic {
            left: 0;
            &:before {
                background-position: center center;
            }
        }

        .user {
            float: none;
            padding-left: em(95,18);
            padding-right: 0;
            text-align: left;
        }

        .user__pic {
            right: auto;
            left: 0;
        }

        .user__name {
            margin-right: em(15,18);
        }

        .user__link {
            margin-left: 0;
        }
    }
}

.feature_bgmod {
    @include tablet {

        .feature__pic {
            &:after {
                content: "";
                position: absolute;
                z-index: 0;
                bottom: 0;
                left: -1000px;
                right: -1000px;
                height: 50%;
                background: #C5C5C5;
            }
        }

        .user {
            margin: 0 -15px -60px;
            background: #C5C5C5;
            border: solid #C5C5C5;
            border-width: 0 15px 60px 15px;
        }
    }

    @include mobile {
        .user {
            margin: 0 -15px -30px;
            background: #C5C5C5;
            border: solid #C5C5C5;
            border-width: 0 15px 30px 15px;
        }
    }
}



.feature-more {
    padding: 50px 0;
    background: #E9E9E9;

    .title {
        margin-bottom: 48px;
        text-align: center;
    }

    .figure {
        margin-bottom: 10px;
    }

    .btn {
        margin-top: 40px;
    }

    .btn_more {
        display: none;
    }

    @include tablet {
        .feature-more__item {
            width: 50%;
        }
    }

    @include mobile {
        padding: 25px 0;
        @include fs(14);

        .title {
            margin-bottom: 24px;
        }

        .btn {
            margin-top: 15px;
        }

        .feature-more__item {
            font-size: 14px;
        }

        .btn_more {
            display: block;
        }

        .feature-more__other {
            display: none;
            text-align: center;
            padding-bottom: em(20);
            border-bottom: 3px solid rgba($text, 0.4);
            ul {
                width: auto;
                float: none;
                padding: 0;
            }
        }
    }
}

.feature-more__items {
    font-size: 0;
    margin: -25px -10px 0;
}

.feature-more__item {
    display: inline-block;
    vertical-align: top;
    margin-top: 25px;
    width: 25%;
    box-sizing: border-box;
    padding: 0 10px;
    @include fs(18);
    @include serif;
}

.feature-more__other {
    margin: em(25) auto 0;
    padding: em(20) em(20) 0;
    max-width: 930px;
    border-top: 3px solid rgba($text, 0.4);
    @include clr;
    line-height: 1.55;

    ul {
        float: left;
        width: 33.333%;
        box-sizing: border-box;
        padding: 0 em(20);
    }
}




.slider {
    width: 100%;
}

.slide {
    position: relative;
    width: 100%;
    height: 400px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

    @include mobile {
        height: 200px;

        .slide__caption {
            @include fs(15);
        }

        .slide__footer {
            padding-bottom: 18px;
        }
    }
}

.slide__footer {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
    height: percentage(140/400);
    padding-bottom: 35px;
    background: #FFF;
    background: rgba(#FFF, 0.8);
    @include vertical;
}

.slide__caption {
    display: inline-block;
    vertical-align: middle;
    @include serif-i;
    @include fs(30);
}



.figure {

    .icon {
        display: block;
        margin: auto;
        text-align: center;
        @include fs(100);
        margin-bottom: em(40,100);
    }

    @include mobile {
        .icon {
            font-size: 50px;
        }

        .figure__text {
            font-size: 16px;
        }
    }

}

.figure__text {
    display: block;
    @include fs(24);
    @include sans;
    text-align: center;
}


.text {
    @include fs(18);
    @include serif;
}




.brands {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-wrap: wrap;
    min-height: 200px;

    @include mobile {
        min-width: 0;
    }
}

.brands__item {
    display: flex;
    padding: 25px;
    a {
        display: inline-block;
        font-size: 0;
    }

    img {
        display: block;
        max-width: 100%;
    }

    @include r(1000) {
        &:nth-child(1) {
            order: 1;
        }
        &:nth-child(2) {
            order: 2;
        }
        &:nth-child(3) {
            order: 4;
        }
        &:nth-child(4) {
            order: 5;
        }
        &:nth-child(5) {
            order: 3;
        }
    }

    @include mobile {
        padding: 10px;

        img {
            width: 50%;
            margin: auto;
        }
    }
}




.footer {
    padding: 50px 0;
    background: $text;
    color: #FFF;

    .logo {
        display: inline-block;
        vertical-align: middle;
    }

    @include r(1000) {
        .btn {
            min-width: 140px;
        }
    }

    @include r(875) {
        .footer__left {
            float: none;
            text-align: center;
            margin-top: 15px;
        }

        .footer__right {
            float: none;
            text-align: center;
        }

        .footer__row {
            margin-top: 0;
        }

        .logo {
            margin-top: 15px;
        }
    }

    @include mobile {
        padding: 20px 0 30px;
        font-size: 14px;

        .footer__row {
            line-height: 2;
            max-width: 235px;
            margin: auto;
            overflow: hidden;
        }

        .footer__links {
            li {
                font-size: 14px;
                padding: 0 10px;
                &:before {
                    border-width: 2px;
                    margin: -1px 0 0 -2px;
                }
            }
        }

        .footer__copy {
            font-size: 12px;
        }

        .logo {
            font-size: 31px;
        }
    }
}

.footer__row {
    @include clr;
    line-height: 40px;
    margin-top: 80px;

    &:first-child {
        margin-top: 0;
    }
}

.footer__left {
    float: left;
}

.footer__right {
    float: right;
}

.footer__links {
    display: inline-block;
    vertical-align: middle;
    margin: 0 -10px;
    font-size: 0;
    @include serif;
    overflow: hidden;

    li {
        display: inline-block;
        position: relative;
        vertical-align: top;
        padding: 0 12px;
        @include fs(18);

        &:before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: 0;
            height: 0;
            border: 3px solid;
            border-radius: 50%;
            margin: -2px 0 0 -3px;
        }

        &:first-child {
            // margin-left: -10px;
            &:before {
                display: none;
            }
        }

        // &:last-child {
        //     margin-right: -10px;
        // }
    }

    a {
        color: inherit;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
}

.footer__copy {
    @include fs(14);
    color: #868686;

    > a {
        display: inline-block;
        margin-right: em(15);
        color: inherit;
    }

    > span, > a {
        display: inline-block;
        vertical-align: bottom;
        line-height: 1.2;
    }
}